footer.footer {
  margin-top: 40px;
  font-size: 87.5%;
  ul {
    list-style-type: none;
    padding: 0;
    margin: 0 0 24px 0;
  }
}

.prefooter.prefooter {
  display: block;
  height: 88px;
}

.footer_bottom {
  border-bottom: 32px solid var(--color-foreground);
  .footer__copyright {
    text-align: center;
    font-size: 12px;
    margin-bottom: 8px;
  }
}

.footer__payment {
  margin-bottom: 40px;
  ul {
    margin: 0;
    padding: 0;
    text-align: center;
    list-style-type: none;
    li {
      display: inline-block;
      margin: 2px 4px;
    }
  }
}